.under-development-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 20px;
  background-color: #f8f9fa;
}

.construction-container {
  max-width: 600px;
  text-align: center;
  padding: 40px;
  background-color: white;
  border-radius: 16px;
}


.icon-container {
  position: relative;
  width: 120px;
  height: 120px;
  margin: 0 auto 30px;
}

.construction-icon {
  position: absolute;
  color: #FF6B00;
}

.main-icon {
  font-size: 60px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3;
}

.tools-icon {
  font-size: 40px;
  bottom: 10px;
  right: 0;
  transform: rotate(15deg);
  animation: wiggle 4s ease-in-out infinite;
  z-index: 2;
}

.cog-icon {
  font-size: 35px;
  top: 15px;
  left: 10px;
  z-index: 1;
}

.spinning {
  animation: spin 8s linear infinite;
}

@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

@keyframes wiggle {
  0%, 100% { transform: rotate(15deg); }
  50% { transform: rotate(-5deg); }
}

h1 {
  font-size: 28px;
  margin-bottom: 20px;
  color: #333;
}

.development-message {
  font-size: 16px;
  line-height: 1.6;
  color: #666;
  margin-bottom: 30px;
}

.progress-container {
  margin-top: 30px;
}

.progress-bar {
  width: 100%;
  height: 8px;
  background-color: rgba(255, 107, 0, 0.1);
  border-radius: 4px;
  overflow: hidden;
  margin-bottom: 10px;
}

.progress-indicator {
  height: 100%;
  width: 30%;
  background-color: #FF6B00;
  border-radius: 4px;
  animation: progress 2s ease-in-out infinite alternate;
}

@keyframes progress {
  from { transform: translateX(-100%); }
  to { transform: translateX(350%); }
}

.progress-text {
  font-size: 14px;
  color: #777;
  margin: 10px 0 0;
} 