@font-face {
  font-family: 'Press Start 2P';
  src: url('../../assets/fonts/PressStart2P.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Rubik';
  src: url('../../assets/fonts/Rubik-VariableFont_wght.ttf') format('truetype-variations');
  font-weight: 100 900;
  font-style: normal;
}

@font-face {
  font-family: 'Integral CF';
  src: url('../../assets/fonts/IntegralCF-Regular.otf') format('opentype');
  font-weight: normal;
  font-style: normal;
}

:root {
  --primary-color: #FF6B00;
  --secondary-color: #f5f5f5;
  --text-color: #333;
  --text-secondary: #666;
  --background-color: #f9f9f9;
  --shadow: 0 2px 10px rgba(0, 0, 0, 0.08);
  /* --titlebar-height: 38px; */
  --titlebar-height: 0px;
  --header-height: 70px; 
  --footer-height: 70px;
  --header-offset: calc(var(--titlebar-height) + var(--header-height));
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  height: 100%;
  overflow: hidden;
}

body {
  font-family: 'Rubik', sans-serif;
  background-color: var(--background-color);
  color: var(--text-color);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

button, input, select, textarea {
  font-family: inherit;
}

::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #ddd;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--primary-color);
}

.app-container {
  display: flex;
  height: calc(100vh - var(--titlebar-height));
}

.map-page-transition {
  transition: all 0.6s cubic-bezier(0.16, 1, 0.3, 1);
}

.main-content {
  flex: 1;
  margin-left: 75px;
  overflow-y: auto;
  height: calc(100vh - var(--header-offset));
}

.main-content.map-page {
  overflow: hidden;
}

@keyframes fadeIn {
  from { 
    opacity: 0;
    transform: translateY(10px);
  }
  to { 
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInFromTop {
  from { 
    transform: translateY(-30px) scale(0.95);
    opacity: 0;
  }
  to { 
    transform: translateY(0) scale(1);
    opacity: 1;
  }
}

@keyframes slideInFromBottom {
  from { 
    transform: translateY(30px) scale(0.95);
    opacity: 0;
  }
  to { 
    transform: translateY(0) scale(1);
    opacity: 1;
  }
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(-30px) scale(0.95);
  }
  to {
    opacity: 1; 
    transform: translateX(0) scale(1);
  }
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(30px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateX(0) scale(1);
  }
}

@keyframes scaleIn {
  from { 
    transform: scale(0.95);
    opacity: 0;
    filter: blur(5px);
  }
  to { 
    transform: scale(1);
    opacity: 1;
    filter: blur(0);
  }
}
.settings-content  {
  animation: fadeInRight 0.9s cubic-bezier(0.16, 1, 0.3, 1);
}
.settings-sidebar, .sidebar {
  animation: fadeInLeft 0.9s cubic-bezier(0.16, 1, 0.3, 1);
}

.player-section, .map-container, .construction-container, .hello-modal, .blocked-modal {
  animation: scaleIn 0.9s cubic-bezier(0.16, 1, 0.3, 1);
}

.welcome-section, .modpacks-header, .header {
  animation: slideInFromTop 0.9s cubic-bezier(0.16, 1, 0.3, 1);
}

.server-section, .modpacks-grid, .footer {
  animation: slideInFromBottom 1s cubic-bezier(0.16, 1, 0.3, 1);
}

