.map-container {
    border: 0;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    box-sizing: border-box;
    z-index: 10;
}

.map-container iframe {
    border: none;
    display: block;
    width: 105%;
    height: 125%;
    position: absolute;
    top: -25%;
    left: 0;
    z-index: 20;
}