.hello-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.hello-modal-overlay.visible {
  opacity: 1;
}

.hello-modal-overlay.hidden {
  opacity: 0;
  pointer-events: none;
}

.hello-modal {
  background-color: #fff;
  border-radius: 15px;
  width: 90%;
  max-width: 450px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
  transform: translateY(0);
  transition: transform 0.3s ease;
  overflow: hidden;
}

.hello-modal-overlay.hidden .hello-modal {
  transform: translateY(20px);
}

.hello-modal-content {
  padding: 30px;
}

.hello-modal h2 {
  font-size: 24px;
  color: #333;
  margin: 0 0 15px 0;
  text-align: center;
}

.hello-modal p {
  font-size: 16px;
  color: #666;
  margin-bottom: 25px;
  text-align: center;
}

.input-group {
  margin-bottom: 20px;
  position: relative;
}

.hello-modal input {
  width: 100%;
  padding: 14px;
  border: 2px solid #e0e0e0;
  border-radius: 8px;
  font-size: 16px;
  transition: border-color 0.2s ease;
  background-color: #f9f9f9;
}

.hello-modal input:focus {
  outline: none;
  border-color: #FF6B00;
  background-color: #fff;
}

.hello-modal input.error {
  border-color: #ff3333;
}

.error-message {
  color: #ff3333;
  font-size: 14px;
  margin-top: 8px;
}

.submit-button {
  width: 100%;
  padding: 14px;
  background-color: #FF6B00;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s ease, transform 0.2s ease;
}

.submit-button:hover {
  background-color: #FF8124;
  transform: translateY(-2px);
}

.submit-button:active {
  transform: translateY(0);
}
