.blocked-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 42, 42, 0.226);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.blocked-modal-overlay.visible {
  opacity: 1;
}

.blocked-modal-overlay.hidden {
  opacity: 0;
  pointer-events: none;
}

.blocked-modal {
  background-color: #fff;
  border-radius: 15px;
  width: 90%;
  max-width: 450px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
  transform: translateY(0);
  transition: transform 0.3s ease;
  overflow: hidden;
}

.blocked-modal-overlay.hidden .blocked-modal {
  transform: translateY(20px);
}

.blocked-modal-content {
  padding: 30px;
}

.blocked-modal h2 {
  font-size: 24px;
  color: #ff3333;
  margin: 0 0 15px 0;
  text-align: center;
}

.blocked-modal p {
  font-size: 16px;
  color: #444;
  margin-bottom: 20px;
  line-height: 1.5;
  text-align: center;
}

.blocked-modal .username {
  font-weight: 600;
  color: #333;
}

.blocked-modal .ds-link {
  color: #4a76a8;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.2s;
}

.blocked-modal .ds-link:hover {
  color: #2a5885;
  text-decoration: underline;
}

.close-button {
  width: 100%;
  padding: 14px;
  background-color: #e9e9e9;
  color: rgb(250, 52, 52);
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s ease, transform 0.2s ease;
  margin-top: 10px;
}
.unban-button {
    width: 100%;
    padding: 14px;
    background-color: #ff3535;
    color: rgb(255, 255, 255);
    border: none;
    border-radius: 8px;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    transition: background-color 0.2s ease, transform 0.2s ease;
    margin-top: 10px;
}

.unban-button:hover {
    background-color: #ff5555;
}
.close-button:hover {
  background-color: #ffdcdc;

}

.close-button:active {
  transform: translateY(0);
}
